
import {add_2_lists, hextorgb, rgbtohex} from './miscelaneous'
import { useSpring, animated, config } from 'react-spring'

export function Landing_ui(){
  const styles1 = useSpring({
    loop: true,
    from: { rotateZ:-2, y:15, },
    to: { rotateZ:1,  y:-15, },
    delay: 0,
    config:{ mass: 30, tension: 100, friction: 0 }
  });
  const styles2 = useSpring({
    loop: true,
    from: { rotateZ:-2, y:15, },
    to: { rotateZ:1,  y:-15, },
    delay: 100,
    config:{ mass: 30, tension: 100, friction: 0 }
  });
  const styles3 = useSpring({
    loop: true,
    from: { rotateZ:-2, y:15, },
    to: { rotateZ:1,  y:-15, },
    delay: 200,
    config:{ mass: 30, tension: 100, friction: 0 }
  });
  return(
    <div>
    <animated.h1 style={{ textAlign: 'center', position:'relative', top: '60px', color:'#C2B280', ...styles1}}>
    Welcome to my site!
    </animated.h1>
    <animated.h2 style={{ textAlign: 'center', position:'relative', top: '60px', color:'#C2B280',...styles2}}>
    My name is Dan and I love making silly things.
    </animated.h2>
    <animated.h3 style={{ textAlign: 'center', position:'relative', top: '70px', color:'#C2B280',...styles3}}>
    Still under construction!
    </animated.h3>
    </div>
  )
}
export function Boombox_ui({music_state}){

  const styles1 = useSpring({
    pause:!music_state, loop: true,
    from: { rotateZ:-10, y:20, },
    to: { rotateZ:10,  y:-20, },
    delay: 0,
    config:{ mass: 10, tension: 100, friction: 0 }
  });
  const styles2 = useSpring({
    pause:!music_state, loop: true,
    from: { rotateZ:-10, y:20, },
    to: { rotateZ:10,  y:-20, },
    delay: 200,
    config:{ mass: 10, tension: 100, friction: 0 }
  });
  const styles3 = useSpring({
    pause:!music_state, loop: true,
    from: { rotateZ:-10, y:20, },
    to: { rotateZ:10,  y:-20, },
    delay: 400,
    config:{ mass: 10, tension: 100, friction: 0 }
  });
  const styles4 = useSpring({
    pause:!music_state, loop: true,
    from: { rotateZ:-10, y:20, },
    to: { rotateZ:10,  y:-20, },
    delay: 600,
    config:{ mass: 10, tension: 100, friction: 0 }
  });
  return(
    <div>
    <h2 style={{ textAlign: 'center', position:'relative', top: '30px', color:'#C2B280'}}>
      <p>
      Play some music!
      </p>
      <p>
      {music_state ? 'Audio is on ♫':'Audio is off' }
      </p>
      <div style={{}}>
        <animated.p style={{position:'relative', top: 40, left:-160, ...styles1}}>
          ♫
        </animated.p>
        <animated.p style={{position:'relative', top: -10, left:-40, ...styles2}}>
          ♫
        </animated.p>
        <animated.p style={{position:'relative', top: -65, left:60, ...styles3}}>
          ♫
        </animated.p>
        <animated.p style={{position:'relative', top: -120, left:160, ...styles4}}>
          ♫
        </animated.p>
        </div>
    </h2>

    </div>
  )
}
