import { useSpring, animated, config } from 'react-spring'






export default function Loading_screen({visible_flag}){
  const props = useSpring({

    loop: true,
    to: { opacity: 1, top:'50%', },
    from: { opacity: 0, top:'0%', },
    config: config.molasses,
  })

return(
  <div>
    <animated.h1 style={{position:'absolute', visibility:visible_flag, width:'100vw', textAlign: 'center',  color:'white', ...props}}>
      <p>LOADING...</p>
      <p>Dandepaz.net</p>
    </animated.h1>
  </div >
)
}
